.navbar {
  background-color: #b31317;
  height: 40px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  padding-top: 5px;
  background:linear-gradient(to top,#f15d5d, #b31317);
  width: 150px;
  height: 100vh;
  display: block;
  justify-content: center;
  position: fixed;
  top: 40px;
  left: -100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius:10px;
  font-family:'Montserrat', sans-serif;
  font-weight:'700'
}
.nav-number{
  color: #b31317;

}

.nav-menu.active {
  left: 0;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
  
}

.nav-text a {
  text-decoration: none;
  color:white;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 8px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color:#e63030;
}
